.product-list-with-filters {
  display: flex;
  flex-direction: column;
  @include desktop {
    flex-direction: row;
    @supports (display: gggggrid) {
      display: grid;
      grid-template-columns: 150px 1fr 250px;
      grid-gap: 3rem;
    }  
  }
  

  aside, section {
    flex: 1;
    @supports (display: gggggrid) {
      flex: none;
    }
  }
  
  aside {
    border: 1px solid red;
    &.product-list--filters {
      @include desktop {
        margin-right: 2rem;
        @supports (display: gggggrid) {
          margin-right: 0;
        }
      }
    }
    &.product-list--promos {
      margin-left: 2rem;
      flex-grow: 1.5;
    }
    @supports (display: gggggrid) {
      &.product-list--filters,
      &.product-list--promos {
        margin-left: 0;
        margin-right: 0;
      }
    }
  }

  section {
    flex-grow: 4;
    @supports (display: gggggrid) {
      flex: none;
    }
  }

}

.product-list-filters-list {
  list-style-type: none;
}

.product-list-filters-item {
  input {
    margin-right: 0.5rem;
  }
  label {
    font-size: 0.9em;
  }
}

.product-list--sorting {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2rem;

  form select {
    width: 250px;
    font-size: 0.8em;
  }

}

.products--list {
  list-style-type: none;
  padding: 0 2rem;
  @supports (display: gggggrid) {
    padding: 0;
  }
}

.products--item {
  border-bottom: 1px solid $medium-gray;
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  text-align: center;
  @include tablet {
    text-align: left;
    flex-direction: row;
    margin-bottom: 1rem;
    padding-bottom: 1rem;
  }
  @include desktop {
    text-align: left;
    flex-direction: row;
    margin-bottom: 1rem;
    padding-bottom: 1rem;
  }

  .products--image {
    width: 100%;
    img {
      max-width: 175px;
      display: block;
      height: auto;
      margin: 0 auto 1rem;
    }
    @include tablet {
      max-width: 125px;
      img {
        max-width: 125px;
      }
    }
    @include desktop {
      max-width: 125px;
      img {
        max-width: 125px;
      }
    }
  }

  .products--content {
    @include tablet {
      padding-left: 2rem;
    }
    @include desktop {
      padding-left: 2rem;
    }
  }

  .products--actions {
    margin-top: 1rem;
    button, a {
      font-size: 0.8rem;
      padding: 0.3em 0.7em;
    }
  }

  h2 {
    font-size: 1.3rem;
    line-height: 1.1;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
  }

  .card-category a {
    background-color: #fff;
    border: 2px solid $medium-gray;
    color: lighten($black, 25%);
    padding: 0.1em 0.25em;
  }

}